.TodoItem{
    background-color: #fafafa;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    margin-left: 24px;
    margin-right: 24px;
    box-shadow: 0px 5px 50px rgba(32, 25, 42, 0.15);
    
}
.TodoItem-p{
    margin:24px 0 24px 24px;
    width: calc(100%- 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}
.TodoItem-p--complete{
    text-decoration: line-through;
}
